import {Injectable} from '@angular/core';
import {CustomerUrlBase} from './../customer-url-base';

@Injectable({
	providedIn: 'root'
})
export class CustomerUrlPassword extends CustomerUrlBase {
	postEmail() {
		return this.customerUrl + '/customer/forgot-password';
	}
	postResetPassWord() {
		return this.customerUrl + '/customer/reset-password';
	}
}
