import {Observable} from 'rxjs';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ForgotPasswordRequest} from 'src/app/core/models/customer/password/request/ForgotPasswordRequest.ViewModel';
import {CustomerUrlPassword} from 'src/app/core/utils/URL/customer-url/customer-password/customer-url-password';
import { ResetPasswordRequest } from 'src/app/core/models/customer/password/request/ResetPasswordRequest.ViewModel';

@Injectable({
	providedIn: 'root'
})
export class PasswordService {
	constructor(private http: HttpClient, private customerUrlPassword: CustomerUrlPassword) {}
	postEmail(forgotPasswordRequest: ForgotPasswordRequest): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlPassword.postEmail(), forgotPasswordRequest);
	}
	postResetPassword(resetPasswordRequest: ResetPasswordRequest): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlPassword.postResetPassWord(), resetPasswordRequest);
	}
}
