import {environment} from 'src/environments/environment';
import {PasswordService} from './../../../../../../../core/services/customer/pass-word/password.service';
import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {first} from 'rxjs/internal/operators/first';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {ForgotPasswordRequest} from 'src/app/core/models/customer/password/request/ForgotPasswordRequest.ViewModel';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';

@Component({
	selector: 'app-customer-forgot-password',
	templateUrl: './customer-forgot-password.component.html',
	styleUrls: ['./customer-forgot-password.component.less'],
	providers: [DestroyService]
})
export class CustomerForgotPasswordComponent implements OnInit {
	submitted = false;
	isLoading = false;
	returnUrl: string;
	clientResetUrl: string = environment.clientUrlForgotPassword;
	customerForgotPasswordForm: FormGroup;
	constructor(
		private formBuilder: FormBuilder,
		//private router: Router,
		private destroy$: DestroyService,
		private passwordService: PasswordService,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService
	) {
		this.returnUrl = '';
		//init form
		this.customerForgotPasswordForm = this.formBuilder.group({
			email: ['', [Validators.required, Validators.minLength(3), Validators.email]]
		});
	}

	ngOnInit(): void {
		console.log(this.clientResetUrl);
		//this.initForm();
	}
	initForm() {
		try {
			this.customerForgotPasswordForm = this.formBuilder.group({
				email: ['', [Validators.required, Validators.minLength(3), Validators.email]]
			});
		} catch (error) {}
	}
	submitForm() {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		try {
			this.submitted = true;
			if (this.customerForgotPasswordForm.invalid) {
				return;
			} else {
				this.isLoading = true;
				const val = this.customerForgotPasswordForm.value;
				
				const forgotPasswordRequest: ForgotPasswordRequest = {
					email: val.email,
					clientResetUrl: this.clientResetUrl
				};
				this.passwordService
					.postEmail(forgotPasswordRequest)
					.pipe(first(), takeUntil(this.destroy$))
					.subscribe(
						(result) => {
							if (result) {
								if (result.isSuccessed) {
									if (result.message) {
										this.notificationService.showSuccessNotification(result.message);
									}
								} else {
									if (result.message) {
										this.notificationService.showErrorNotification(result.message);
									}
								}
							} else {
							}
							this.submitted = false;
							this.isLoading = false;
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						},
						(error) => {
							this.submitted = false;
							this.isLoading = false;
							this.appErrorHandler.handleErrorWithNoti(error);
						}
					);
			}
		} catch (error) {
			this.submitted = false;
			this.isLoading = false;
			this.appErrorHandler.handleErrorWithNoti(error);
		}
	}
}
