import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {first} from 'rxjs/internal/operators/first';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ResetPasswordRequest} from 'src/app/core/models/customer/password/request/ResetPasswordRequest.ViewModel';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import { AuthService } from 'src/app/core/services/auth/auth.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {PasswordService} from 'src/app/core/services/customer/pass-word/password.service';

@Component({
	selector: 'app-customer-reset-password',
	templateUrl: './customer-reset-password.component.html',
	styleUrls: ['./customer-reset-password.component.less']
})
export class CustomerResetPasswordComponent implements OnInit {
	submitted = false;
	isLoading = false;
	// returnUrl: string;
	// clientResetUrl: string = environment.clientUrlForgotPassword;
	customerResetPasswordForm: FormGroup;
	constructor(
		private formBuilder: FormBuilder,
		private activatedRoute: ActivatedRoute,
		private destroy$: DestroyService,
		private passwordService: PasswordService,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService,
		private authService: AuthService,
		private router: Router
	) {
		//this.returnUrl = '';
		//init form
		this.customerResetPasswordForm = this.formBuilder.group({
			password: ['', [Validators.required, Validators.minLength(3)]],
			passwordComfirm: ['', [Validators.required, Validators.minLength(3)]]
		});
	}

	ngOnInit(): void {}
	submitForm() {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		try {
			this.submitted = true;
			if (this.customerResetPasswordForm.invalid) {
				return;
			} else {
				this.isLoading = true;
				const val = this.customerResetPasswordForm.value;

				const resetPasswordRequest: ResetPasswordRequest = {
					password: val.password,
					confirmPassword: val.passwordComfirm,
					email: this.activatedRoute.snapshot.queryParams['email'],
					token: this.activatedRoute.snapshot.queryParams['token']
				};
				this.passwordService
					.postResetPassword(resetPasswordRequest)
					.pipe(first(), takeUntil(this.destroy$))
					.subscribe(
						(result) => {
							if (result) {
								if (result.isSuccessed) {
									this.logout();
									if (result.message) {
										this.notificationService.showSuccessNotification(result.message);
									}
								} else {
									if (result.message) {
										this.notificationService.showErrorNotification(result.message);
									}
								}
							} else {
							}
							this.submitted = false;
							this.isLoading = false;
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						},
						(error) => {
							this.submitted = false;
							this.isLoading = false;
							this.appErrorHandler.handleErrorWithNoti(error);
						}
					);
			}
		} catch (error) {
			this.submitted = false;
			this.isLoading = false;
			this.appErrorHandler.handleErrorWithNoti(error);
		}
	}
	logout() {
		this.authService.logout('customer', this.router);
		// this.store.dispatch(customerLoginResetAction({message: 'logout'}));
		// this.localStore.removeItem('customer-token');
		// this.router.navigate(['/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
	}
}
